    <RouterView :control-app-loading="controlAppLoading" v-if="data.showSubPage" :app-data="data" style="width: 100vw;height: 100vh;"></RouterView>
    <div v-if="data.showLoading!=2" style="width: 100vw;height: 100vh;padding-left: 50vw;padding-top: 50vh;position: fixed;left: 0;top: 0;z-index: 20000;transition: opacity .3s cubic-bezier(0.23, 1, 0.320, 1);"
      :style="{
        backgroundColor:data.theme.background,
        opacity:data.showLoading==0?1:0
      }"
    >
      <img src="./assets/logo.png" style="width: 100px;transform: translate(-50%,-50%);" />
      <div style="width: 100vw;text-align: center;position: fixed;left: 0;bottom: 25vh;padding-top: 50px;">
        <p v-if="data.showLoadingText" style="color: var(--color-neutral-10);font-size: 24px;">后端服务未启动</p>
        <div v-if="data.showLoadingAnm" style="--size: 32px; --dot-size: 4px; --dot-count: 6; --color: var(--color-neutral-10); --speed: 1s; --spread: 60deg;display: inline-block;transform: translateY(50%);" class="dots">
            <div style="--i: 0;" class="dot"></div>
            <div style="--i: 1;" class="dot"></div>
            <div style="--i: 2;" class="dot"></div>
            <div style="--i: 3;" class="dot"></div>
