<script>
import config from '@/config'
import axios from 'axios'
import tool from './utlis/tool'

export default{
  computed:{
    localStorage(){
      return localStorage
    }
  },
  data(){
    return{
      data:{
        showLoadingText:false,
        showLoadingAnm:false,
        showSubPage:false,
        showLoading:0,
        isMobile:undefined,
        isDarkMode:false,
        serverURL:'',
        serverWSURL:'',
        theme:{
          background:'',
          translucentBackground:''
        },
        pageSize:{
          width:0,
          height:0
        },
        mousePosition:{
          x:0,
          y:0
        }
      }
    }
  },
  created() {
    this.data.serverURL = config.serverURL
    this.data.serverWSURL = config.serverWSURL
  },
  mounted(){

    axios.get(config.serverURL.split('/api/v1')[0]).then(async res=>{
      this.data.showSubPage = true
      await tool.waitSeconds(0.5)
      this.data.showLoadingAnm = true
    }).catch(err=>{
      this.data.showLoadingAnm = false
      this.data.showLoadingText = true
    })

    this.data.isMobile = /Mobile/i.test(navigator.userAgent)
    
    //一直检测深色模式是否开启
    const prefersDarkMode = window.matchMedia('(prefers-color-scheme: dark)');
    if (!prefersDarkMode.matches) {
      //浅色模式
      this.data.theme.background = 'white'
      this.data.theme.translucentBackground = 'rgba(242,242,242,0.6)'
      this.data.isDarkMode = false
      document.body.removeAttribute('arco-theme')
    } else {
      //深色模式
      this.data.theme.background = 'rgb(15,15,15)'
      this.data.theme.translucentBackground = 'rgba(25,25,25,0.6)'
      this.data.isDarkMode = true
      document.body.setAttribute('arco-theme', 'dark')
    }

    //更新实时页面宽高
    setInterval(() => {
      this.data.pageSize.width = document.getElementById('appWrapper').clientWidth
      this.data.pageSize.height = document.getElementById('appWrapper').clientHeight
    }, 1);

    this.controlAppLoading()
  },
  methods:{
    async controlAppLoading(){
      this.data.showLoading = 1
      await tool.waitSeconds(0.3)
      this.data.showLoading = 2
    },
    startok(){
      localStorage.setItem('allow',true)
      location.reload()
    },
    getMousePosition(e){
      this.data.mousePosition.x = e.clientX
      this.data.mousePosition.y = e.clientY
    }
  },
  props:{

  },
}
</script>

